import React from "react";
import { Group, Div, Button, Panel, PanelHeader } from "@vkontakte/vkui";
import connect from "@vkontakte/vk-bridge";
import "./main.css";
import axios from "axios";

let user_id = 612267237;
let balance = 1;
let bonusbalance = 1;
let user_data_name = "///";
let user_data_name2 = "///";
async function get() {
  let user_info = await connect.sendPromise("VKWebAppGetUserInfo");
  user_data_name = user_info.data.first_name;
  user_data_name2 = user_info.data.last_name;
  user_id = Number(user_info.data.id);

  let getbal = await axios({
    method: "POST",
    url: `https://lucky-coin.ru/user/balance`,
    headers: {
      "Content-Type": "application/json",
    },
    data: {
      id: Number(user_id),
    },
  });
  balance = getbal.data.response.balance;
  bonusbalance = getbal.data.response.bonusbalance;
}
get();
const main = (props) => (
  <Panel id={props.id}>
    <PanelHeader>Профиль</PanelHeader>
    ID ({user_id}) Имя ({user_data_name}) Фамилия ({user_data_name2})
    {parseFloat(balance).toFixed(0)}
    {parseFloat(bonusbalance).toFixed(0)}
    <Button size="xl" level="2" onClick={() => get()}>
      Обновить
    </Button>
  </Panel>
);

export default main;
